import { createStore } from 'vuex';

export default createStore({
  state: {
    basket: []
  },
  mutations: {
    addToBasket(state, product) {
      const existingProduct = state.basket.find(item => item.id === product.id);
      if (existingProduct) {
        existingProduct.quantity++;
      } else {
        state.basket.push({ ...product, quantity: 1 });
      }
    },
    removeFromBasket(state, productId) {
      state.basket = state.basket.filter(item => item.id !== productId);
    },
    updateQuantity(state, { productId, quantity }) {
      const product = state.basket.find(item => item.id === productId);
      if (product) {
        product.quantity = quantity;
      }
    }
  },
  getters: {
    basket(state) {
      return state.basket; // Ensure you have a getter for basket
    },
    totalAmount(state) {
      return state.basket.reduce((total, product) => {
        return total + product.price * product.quantity;
      }, 0);
    }
  }
});
