<template>
    <div v-if="isVisible" class="modal-overlay" @click.self="close">
      <div class="modal-content">
        <h2>Your Basket</h2>
        <div v-if="basketItems.length === 0">Your basket is empty.</div>
        <div v-for="item in basketItems" :key="item.id" class="basket-item">
          <p>{{ item.title }} (x{{ item.quantity }})</p>
          <p>{{ item.price }} USD each</p>
          <p>Total: {{ (item.price * item.quantity).toFixed(2) }} USD</p>
          <button @click="removeItem(item.id)">Remove</button>
          <input type="number" v-model="item.quantity" @input="updateQuantity(item.id, item.quantity)" min="1" />
        </div>
        <h3>Total Amount: {{ totalAmount.toFixed(2) }} USD</h3>
        <button @click="close">Close</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: ['isVisible'],
    computed: {
      basketItems() {
        return this.$store.state.basket;
      },
      totalAmount() {
        return this.$store.getters.totalAmount;
      }
    },
    methods: {
      close() {
        this.$emit('close');
      },
      removeItem(productId) {
        this.$store.commit('removeFromBasket', productId);
      },
      updateQuantity(productId, quantity) {
        if (quantity > 0) {
          this.$store.commit('updateQuantity', { productId, quantity });
        } else {
          this.removeItem(productId); // Remove item if quantity is 0
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    width: 400px;
  }
  .basket-item {
    border-bottom: 1px solid #eee;
    padding: 10px 0;
  }
  </style>
  