<template>
  <div class="basket">
    <h2>Your Basket</h2>
    <div v-if="basketItems.length === 0">Your basket is empty.</div>
    <div v-for="item in basketItems" :key="item.id" class="basket-item">
      <p>{{ item.title }} (x{{ item.quantity }})</p>
      <p>{{ item.price.toFixed(2) }} USD each</p>
      <p>Total: {{ (item.price * item.quantity).toFixed(2) }} USD</p>
      <button @click="removeItem(item.id)">Remove</button>
      <input type="number" v-model.number="item.quantity" @input="updateQuantity(item.id, item.quantity)" min="1" />
    </div>
    <h3>Total Amount: {{ totalAmount.toFixed(2) }} USD</h3>
  </div>
</template>

<script>
export default {
  computed: {
    basketItems() {
      return this.$store.getters.basket; // Access the basket from getters
    },
    totalAmount() {
      return this.$store.getters.totalAmount; // Use getter for total amount
    }
  },
  methods: {
    removeItem(productId) {
      this.$store.commit('removeFromBasket', productId);
    },
    updateQuantity(productId, quantity) {
      if (quantity > 0) {
        this.$store.commit('updateQuantity', { productId, quantity });
      } else {
        this.removeItem(productId); // Remove item if quantity is 0
      }
    }
  }
};
</script>

<style scoped>
.basket {
  border: 1px solid #ddd;
  padding: 16px;
  border-radius: 8px;
  margin-top: 20px;
}
.basket-item {
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
.basket-item:last-child {
  border-bottom: none; /* Remove border from the last item */
}
button {
  background-color: #d9534f; /* Bootstrap danger color */
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}
button:hover {
  background-color: #c9302c; /* Darker red on hover */
}
</style>
