<template>
    <div class="product-grid">
      <div v-for="product in filteredProducts" :key="product.id" class="product-item">
        <img :src="product.thumbnail" :alt="product.title" />
        <h3>{{ product.title }}</h3>
        <p>{{ product.description }}</p>
        <p>{{ product.price }} USD</p>
        <button @click="addToBasket(product)">Add to Basket</button>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    props: {
      searchQuery: {
        type: String,
        required: true
      },
      selectedCategory: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        products: []
      };
    },
    computed: {
      filteredProducts() {
        return this.products.filter(product => {
          return (
            (this.selectedCategory === '' || product.category === this.selectedCategory) &&
            product.title.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        });
      }
    },
    created() {
      axios.get('https://dummyjson.com/products')
        .then(response => {
          this.products = response.data.products;
        })
        .catch(error => {
          console.error('Error fetching products:', error);
        });
    },
    methods: {
      addToBasket(product) {
        console.log('Adding to basket:', product); // Debugging line
    this.$store.commit('addToBasket', product);
    this.$emit('add-to-basket'); // Emit event to trigger modal opening
    console.log('Current basket state:', this.$store.state.basket); // Debugging line
      }
    }
  };
  </script>
  
  <style scoped>
  .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 16px;
  }
  
  .product-item {
    border: 1px solid #ddd;
    padding: 16px;
    border-radius: 8px;
    text-align: center;
  }
  
  .product-item img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  .product-item h3 {
    font-size: 1.2em;
    margin: 8px 0;
  }
  
  .product-item p {
    margin: 4px 0;
  }
  
  .product-item button {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .product-item button:hover {
    background-color: #218838;
  }
  </style>
  